import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { routing } from './parent.routing';
import { HttpModule } from '@angular/http';
import { NgaModule } from '../../theme/nga.module';
import { Ng2SmartTableModule } from 'ng2-smart-table';

import { ParentComponent } from './parent.component';
import { ParentManageComponent } from './parent-manage.component';
import { ParentService } from './parent.service';
import { StudentService } from '../student/student.service';

// import { SearchFilterPipe } from 'app/services/search-filter.pipe';
import { PaginationService } from '../../services/pagination.service';
// import { DomSanitizer } from '@angular/platform-browser/src/security/dom_sanitization_service';
import { DatePickerModule } from 'ng2-datepicker';

const parentComponentList = [ParentComponent];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    routing,
    NgaModule,
    HttpModule,
    Ng2SmartTableModule,
    DatePickerModule,
  ],
  declarations: [ParentComponent, ParentManageComponent],
  providers: [ParentService, PaginationService, StudentService],
})

export class ParentModule {


 }



