import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ParentService } from './parent.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/takeWhile';

import { CommonProperties } from '../common-properties';

import { CommonService } from '../../services/common.service';
import { StudentService } from '../student/student.service';

import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { DomSanitizer, SafeResourceUrl, SafeUrl } from '@angular/platform-browser';
import { DatePickerOptions, DateModel } from 'ng2-datepicker';

@Component({
  selector: 'parent',
  templateUrl: './parent.component.html',
  styleUrls: ['./parent.component.css'],
})

export class ParentComponent implements OnInit, OnDestroy {

  isSubscribe: boolean = true;
  id: number;
  parentSelected: any= {};
  parentList: any;
  message: string;
  isErrorMessage: boolean= false;
  classname: string;
  profileImg: string;
  objCurrentDate: Date;
  isUploaded: string;
  isFileSelected: boolean;
  fileList: FileList;
  noImage: string= '../../../assets\\images\\noImage.png';
  secureImage: any;
  studentList: any;
 
  date: any;
  options: DatePickerOptions;
  filteredItems: any[];
  relatedStudents: any= [];
  searchValue: string;

  actives: any = [
    { id: 0, name: 'False' },
    { id: 1, name: 'True' },
  ];

  isSuperAdmin: any = [
    { id: 0, name: 'Parent' },
    { id: 1, name: 'Super Admin' },
    { id: 2, name: 'Teacher | Faculty' },
  ];

  genders: any = [
    { id: 'Male', name: 'Male' },
    { id: 'Female', name: 'Female' },
  ];
 activeIndex: number= -1;
 // selectedRoleIndex: number= -1;
 returnUrl = `parent/manageparent/`;  
 selectedItem: any;

  constructor(private route: ActivatedRoute, private router: Router, private parentService: ParentService, 
    private commonService: CommonService, private _http: Http, private sanitizer: DomSanitizer,
    private studentService: StudentService, private commonProperties: CommonProperties ) {
      this.secureImage = sanitizer.bypassSecurityTrustUrl(this.noImage);
      this.options = new DatePickerOptions(); 
      this.date = new DateModel();
     }


  ngOnInit() {
    // console.log(JSON.parse(localStorage.getItem('currentUser')));
   // this.commonService.readURL('ddd');
    this.objCurrentDate = new Date();
    this.activeIndex = -1;
    // this.selectedRoleIndex = -1;
    this.route.params.subscribe(
      params => {
             this.id = +params['id']; // (+) converts string 'id' to a number
      });
   // get the Parent to update
    if (this.id > 0) {
    this.getParentByID(this.id);
    }
  }
  ngOnDestroy() {
    this.isSubscribe = false;
  }
  
  // Get Parent
  getParent() {
          this.parentService.getParents()
                          .takeWhile(() => this.isSubscribe)
                          .subscribe(resData => this.parentList = resData,
                            err => console.error(err),
                          // () => console.log(this.parentList)
                          );
  }
                        
getParentByID(id) {
            this.parentService.getParentByID(id).takeWhile(() => this.isSubscribe)
              .subscribe(resData => { 
                this.parentSelected = resData; 
              // console.log(this.parentSelected.IsActive);
                this.activeIndex = this.parentSelected.IsActive;
                this.parentSelected.IsActive = parseInt(this.parentSelected.IsActive);
                this.profileImg = this.parentSelected.ProfileImg;
                // 'url(' + CommonProperties.serviceURL+'/UploadFile/'+ this.profileImg + ')'
                this.date.formatted = String(this.parentSelected.DOB).substring(0, 10);
                // student details
                this.parentSelected.StudentDetails.split('~').forEach( el => {
                          this.relatedStudents.push( {ID: el.split(',')[0], FirstName: el.split(',')[1], 
                                            LastName: el.split(',')[2], relation: el.split(',')[3] });
                     
                });

                if (this.profileImg) {
                $('#spnimgProfile').css('background-image',
                                        `url(${CommonProperties.serviceURL}/UploadFile/Parent/${this.profileImg})`);
                }
              },
                err => console.error(err),
      );
                            
}

  HideMessage() {
    this.message = '';
    // return this.isErrorMessage = !this.isErrorMessage;
  }

  // Update Parent
  updateParent(id, parent) {
    parent.ID = id;
    parent.LastUpdatedBy = this.commonProperties.currentUser.ID; //JSON.parse(localStorage.getItem('currentUser')).ID;
    parent.LastModifiedOn = new Date(); 
    parent.ProfileImg = this.profileImg;
    
    this.parentService.updateParent(id, parent).subscribe (
       data => {
         // refresh the list
        // this.getParent();
         this.message = 'Parent updated successfully! ';
        // this.router.navigate([this.returnUrl]);
         return true;
       },
       error => {
         console.error('Error saving Parent!');
         return Observable.throw(error);
       },
    );

  }

onBack() {
  this.router.navigate([this.returnUrl]);
}

 saveParent (parent: any) {
  parent.CreatedBy = this.commonProperties.currentUser.ID;// JSON.parse(localStorage.getItem('currentUser')).ID;
  parent.CreatedOn = new Date(); 
  parent.ProfileImg = this.profileImg;
  //  console.log(Parent.ProfileImg);

  this.parentService.saveParent(parent).takeWhile(() => this.isSubscribe)
                    .subscribe(
                      resData => {
                        // this.getParent();
                        this.isErrorMessage = false;
                        this.classname = '';
                        this.message = 'Data saved successfully! ';
                      },
                      error => {
                        this.isErrorMessage = true;
                        this.message = `Error ${error}`;
                        console.error('Error saving Parent!');
                        return Observable.throw(error);
                  });
  }

  // Save Parent Record
  submitParent(value: any) {
 
    if (!value.form.valid) {
      this.message = 'Please enter required values!';
      this.isErrorMessage = true;
      this.classname = 'danger';
        return;
    }
    value.value.DOB = value.value.DOB.formatted;
          // format student details to save
          let studentDetails: string = '';
          // value.value.StudentDetails= 
          this.relatedStudents.forEach(el => {
            studentDetails = `${studentDetails}~${el.ID},${el.FirstName},${el.LastName},${el.relation}`;
           });
           if ( studentDetails.length > 0) {
           studentDetails = studentDetails.substring(1);
           }
    //  console.log(studentDetails) ;
      value.value.StudentDetails = studentDetails;
   // end
    // update the Parent record
    if (this.id > 0) {
       // save image to folder
      // console.log(this.fileList);
       if (this.fileList !== undefined && this.fileList.length > 0) {
        this.uploadProfile();
      }
          this.updateParent(this.id, value.value);  
          value.form.reset();
          this.relatedStudents = [];
          $('#spnimgProfile').css('background-image', '');
          
    } else {
        // save image to folder
        if (this.fileList !== undefined && this.fileList.length > 0) {
          this.uploadProfile();
        }
          this.saveParent(value.value);
          value.form.reset();
          this.relatedStudents = [];
          $('#spnimgProfile').css('background-image', '');
      }
  }

  // file upload event  
fileChange(event) {  
      this.fileList = event.files;  
      if (this.fileList !== null && this.fileList.length > 0) {
        this.isFileSelected = true;
       // console.log(this.isFileSelected);
        this.profileImg = this.fileList[0].name;
        const file: File = this.fileList[0]; 
        // this.profileImg = `${new Date().getTime().toString()}${file.name}`;
       this.profileImg = `${new Date().getTime().toString()}.${file.name.split('.')[1]}`;
       
    } else {
      this.isFileSelected = false;
    }
}
  
  uploadProfile() {
    this.parentService.uploadProfile(this.fileList, this.profileImg);
    this.isUploaded = this.profileImg;
  }

  onDateChange(dob) {
    if (isNaN(Date.parse(this.date.formatted))) {
     this.date.formatted = this.commonService.formateDate(this.objCurrentDate, 'yyyy-MM-dd');
    }
   }

   getStudentRelation(student) {
    // this.searchValue='';
     let value = window.prompt('Please enter relationship');
     if(value == '') {
      this.getStudentRelation(student);
     } else {
       this.relatedStudents.push( {ID: student.ID, FirstName: student.FirstName, 
                                   LastName: student.LastName, relation: value });
     }

    // console.log(value);
   }

   filterStudentByNameEmail(value) {
    this.filteredItems = [];
    this.searchValue = value;
    if (value != '' && value.length > 2) {
          this.studentList.forEach(element => {
            if (
              (JSON.stringify(element.FirstName).toLowerCase().indexOf(value.toLowerCase()) >= 0) ||
              (JSON.stringify(element.Email).toLowerCase().indexOf(value.toLowerCase()) >= 0)
            ) { // for any thing
          // if (JSON.stringify(element.FirstName).toLowerCase().indexOf(value.toLowerCase()) >= 0 ) {
            this.filteredItems.push(element);
     }
   });
 }
    // else {
    //    this.filteredItems = this.studentList;
    // }
    
   }

   removeStudentRelation(id) {
      this.relatedStudents = this.relatedStudents.filter(e => e.ID != id);
   }

     // Get User
  getStudent() {
    this.searchValue = '';
    this.filteredItems = [];
    this.studentService.getStudents()
                    .subscribe(resData => this.studentList = resData,
                      err => console.error(err),
                    // () => console.log(this.studentList)
                    );
          }


}



