import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import { CommonProperties } from '../common-properties';
import { xhrHeaders } from '../xhr-headers';
import { Observable } from 'rxjs/Observable';
import { CommonService } from 'app/services/common.service';

@Injectable()
export class ParentService {

    private parentServiceUrl = `${CommonProperties.serviceURL}api/parents/`; 

    // preferred to use template literal rather concatination  like below line
    // CommonProperties.serviceURL + 'api/admins/';  // URL to web api 

    constructor(private http: Http, private commonService: CommonService) {}

    getParents(): Observable<any> {
        return this.http.get(this.parentServiceUrl)
                        .map((res: Response) => res.json());
    }
   
    getParentByID(id) {
        return this.http.get(`${this.parentServiceUrl}${id}`)
                .map((res: Response) => res.json());
    }

    getParentByRole(role: string) {
        return this.http.get(`${this.parentServiceUrl}GetParentByRole/${role}`)
                .map((res: Response) => res.json());
    }

    getParentByStudentID(id: number) : Observable<any> {
        return this.http.get(`${this.parentServiceUrl}GetParentByStudentID/${id}`)
                .map((res: Response) => res.json());
    }

    saveParent(parent) {
        const body = JSON.stringify(parent);
        return this.http.post(this.parentServiceUrl, body, xhrHeaders())
            .map((res: Response) => res.json());
    }

    deleteParent(id) {
        return this.http.delete(`${this.parentServiceUrl}${id}`);
   }

  updateParent(id, parent) {
      const body = JSON.stringify(parent);
      return this.http.put(`${this.parentServiceUrl}${id}`, body, xhrHeaders()).map((res: Response) => res.json());
  }

  uploadProfile(fileList, fileName) {
    if (fileList.length > 0) {  
        const file: File = fileList[0];  
        const formData: FormData = new FormData();  
        formData.append('uploadFile', file, fileName);  
       // this.commonService.uploadFile(formData);
        this.commonService.uploadFile(formData, 'Parent');
        }  
     }
}



