import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import { CommonProperties } from '../common-properties';
import { xhrHeaders } from '../xhr-headers';
import { Observable } from 'rxjs/Observable';
import { CommonService } from 'app/services/common.service';

@Injectable()
export class StudentService {

    private studentServiceUrl = `${CommonProperties.serviceURL}api/students/`; 

    // preferred to use template literal rather concatination  like below line
    // CommonProperties.serviceURL + 'api/admins/';  // URL to web api 

    constructor(private http: Http, private commonService: CommonService) {}

    getStudents() {
        return this.http.get(this.studentServiceUrl)
                        .map((res: Response) => res.json());
    }
   
    getStudentByID(id) {
        return this.http.get(`${this.studentServiceUrl}${id}`)
                .map((res: Response) => res.json());
    }

    saveStudent(student) {
        const body = JSON.stringify(student);
        return this.http.post(this.studentServiceUrl, body, xhrHeaders())
            .map((res: Response) => res.json());
    }

    deleteStudent(id) {
        return this.http.delete(`${this.studentServiceUrl}${id}`);
   }

  updateStudent(id, student) {
      const body = JSON.stringify(student);
      return this.http.put(`${this.studentServiceUrl}${id}`, body, xhrHeaders()).map((res: Response) => res.json());
  }

  uploadProfile(fileList, fileName) {
    if (fileList.length > 0) {  
        const file: File = fileList[0];  
        const formData: FormData = new FormData();  
        formData.append('uploadFile', file, fileName);  
       // this.commonService.uploadFile(formData);
        this.commonService.uploadFile(formData, 'Student');
        }  
     }
}



