import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { ParentService } from './parent.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/takeWhile';

import { CommonProperties } from '../common-properties';

import { PaginationService } from '../../services/pagination.service';


@Component({
  selector: 'parent-manage',
  templateUrl: './parent-manage.component.html',
  // styleUrls: ['./smartTables.scss'],
   styleUrls: ['./parent.component.css'],
  
})
export class ParentManageComponent implements OnInit, OnDestroy {

  isSubscribe: boolean = true;
 parentServiceUrl: string;
  title = 'Manage Parent';
  itemList: any;
  query: string = '';
  returnUrl: string;
  parentID: number;

  constructor(private parentService: ParentService, private route: ActivatedRoute, private router: Router, 
              private paginationService: PaginationService ) { }

  ngOnInit() {
    this.parentServiceUrl = CommonProperties.serviceURL;
    this.getParent();
  }

  ngOnDestroy() {
    this.isSubscribe = false;
  }

  // New Parent
  newParent() {
    this.router.navigate(['parent/parent/']);
  }

// Get Parent
  getParent() {
    $('#popup').fadeIn();
    $('#fade').fadeIn();
          this.parentService.getParents().takeWhile(() => this.isSubscribe)
                          .subscribe(
                            resData => {
                            this.paginationService.itemList = resData;
                            $('#popup').fadeOut();
                            $('#fade').fadeOut();
                            },
                            err => console.error(err),
                            () => { 
                              this.paginationService.filteredItems = this.paginationService.itemList;
                              this.paginationService.init();
                           },
                      );
  }

  // Update Parent
  updateParent(id) {
    this.returnUrl = `parent/parent/${id}`;  
    this.router.navigate([this.returnUrl]);
  }

  setParentID(id): void {
   // console.log(id);
    this.parentID = id;
  }
  // Delete Parent
  deleteParent(): void {
    // if (window.confirm(`Are you sure you want to delete ${id}?`)) {
  //  if (window.confirm(`Are you sure you want to delete ?`)) {
      this.parentService.deleteParent(this.parentID).takeWhile(() => this.isSubscribe).subscribe(
         data => {
           // refresh the list
           this.getParent();
           return true;
         },
         error => {
           console.error('Error deleting Parent!');
           return Observable.throw(error);
         },
      );
  //  }
  }

}



